@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#elem{
    text-align:-webkit-center;
}
#elem:hover {
    opacity: 0.99;
}

#more:hover{
    color:'green';
}
#name{
    -webkit-animation: appear 4s infinite linear alternate;
            animation: appear 4s infinite linear alternate;
}
#average{
    -webkit-animation: rotate 2s linear alternate;
            animation: rotate 2s linear alternate;
}
@-webkit-keyframes appear {
    from{
        -webkit-transform: rotateZ(-10deg);
                transform: rotateZ(-10deg);
    }
    to{
        -webkit-transform: rotateZ(-3deg);
                transform: rotateZ(-3deg);
    } 
}
@keyframes appear {
    from{
        -webkit-transform: rotateZ(-10deg);
                transform: rotateZ(-10deg);
    }
    to{
        -webkit-transform: rotateZ(-3deg);
                transform: rotateZ(-3deg);
    } 
}
@-webkit-keyframes rotate {
    from{
        box-shadow: 0px 0px;
    }
    to{
        box-shadow: 5px 15px;
    } 
}
@keyframes rotate {
    from{
        box-shadow: 0px 0px;
    }
    to{
        box-shadow: 5px 15px;
    } 
}
.App {
  text-align: center;
  width:-webkit-fit-content;
  width:-moz-fit-content;
  width:fit-content;
}
body{
  color: #edf5e1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#title{
   color:'white';
   border: '1px solid';
   box-shadow:'5px 10px';
   width:'300px'
}
#elem{
   text-align:-webkit-center;
}
#elem:hover {
   opacity: 0.99;
}
#left{
   -webkit-animation: fromLeft 1s linear alternate;
           animation: fromLeft 1s linear alternate;
}
#right{
   -webkit-animation: fromRight 1s linear alternate;
           animation: fromRight 1s linear alternate;
}
.carousel-inner{
   height: 400px
}
@-webkit-keyframes fromLeft {
   0%{
       -webkit-transform: translateX(-800px);
               transform: translateX(-800px);
       opacity: 0.2;
   }
   50%{
      -webkit-transform: translateX(60px);
              transform: translateX(60px);
       opacity: 1;
   } 
   100%{
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
       opacity: 1;
   } 
}
@keyframes fromLeft {
   0%{
       -webkit-transform: translateX(-800px);
               transform: translateX(-800px);
       opacity: 0.2;
   }
   50%{
      -webkit-transform: translateX(60px);
              transform: translateX(60px);
       opacity: 1;
   } 
   100%{
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
       opacity: 1;
   } 
}
@-webkit-keyframes fromRight {
   0%{
       -webkit-transform: translateX(800px);
               transform: translateX(800px);
       opacity: 0.2;
   }
   50%{
      -webkit-transform: translateX(-60px);
              transform: translateX(-60px);
       opacity: 1;
   } 
   100%{
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
       opacity: 1;
   } 
}
@keyframes fromRight {
   0%{
       -webkit-transform: translateX(800px);
               transform: translateX(800px);
       opacity: 0.2;
   }
   50%{
      -webkit-transform: translateX(-60px);
              transform: translateX(-60px);
       opacity: 1;
   } 
   100%{
      -webkit-transform: translateX(0px);
              transform: translateX(0px);
       opacity: 1;
   } 
}
